body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Modals */
.modals-background {
  background: #ffcc80;
  height: 300px;
  margin: 10px;
}

.popup-modal {
  padding-top: 1px;
}

/* Tictactoe Component Style */
.board-square {
  width: 90px;
  height: 90px;
  background-color: black;
  border-radius: 5px;
  margin: 3px;
  margin-left: 0px;
}

.board {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.gameover-display {
  width: 280px;
  height: 280px;
  background-color: white;
  margin: 0 auto;
  border: 5px solid white;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.gameover-display p {
  color: black;
  margin: 0;
  font-weight: bold;
  border: 2px solid black;
  border-radius: 6px;
  padding: 5px;
}
/* *******  END  ******* */

/* Hangman Component Style */
.hangman-wrapper {
  display: -webkit-flex;
  display: flex;
  padding: 15px;
}

.left-wrapper {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.left-one {
  width: 100%;
  display: -webkit-flex;
  display: flex;

  height: 20%;
}

.left-two {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 80%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.hangman-figure {
  height: 50%;
  width: 30%;
}

.balloon-img {
  width: 100%;
  height: 100%;
}


.letter-placer {
  border-bottom: 1px solid black;
  display: -webkit-flex;
  display: flex;
  width: 15px;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 2px;
}

.right-wrapper {
  width: 20%;
}

.correctletters-placer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 25px;
}

.word-input {
  display: -webkit-flex;
  display: flex;
}

.word-input .ant-input {
  width: 200px;
  margin: -2px 5px 0 5px;
}

.mistakes-wrapper {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  width: 30%;
}

.mistakes-text {
  width: 85px;
  height: 30px;
  background: aquamarine;
  color: black;
}

.alphabet-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0 auto;
  width: 230px;
}

.letter {
  width: 50px;
}

.alphabet-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
/* *******  END  ******* */
